html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Space Mono", monospace;
  min-height: 100vh;
  background-color: #c5e4e7;
}

h1,
h2,
h3 {
  margin-top: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 56.25rem;
  margin: 0 auto;
}

.header {
  text-align: center;
  margin-bottom: 3rem;
}
.header__logo {
  object-fit: contain;
}

.calc {
  background-color: white;
  padding: 1rem;
}

.input {
  padding: 0.3125rem;
}
.input__icon {
  width: 1.25rem;
}
.input__input {
  background-color: #26c0ab;
}